const search = () => {
    const searchInput = document.querySelector('.searcher'); //    
    const gridItems = document.querySelectorAll('.grid-item'); //    

    if (!searchInput) { //     
        console.error('Search input element not found'); //      ,      
        return; //   
    }

    //      
    function showAllItems() {
        gridItems.forEach(item => {
            item.classList.remove('hidden'); //   hidden   
            item.classList.add('fade-in'); //   fade-in    
        });
    }

    //    
    function hideItem(item) {
        item.classList.add('hidden'); //   hidden   
        item.classList.remove('fade-in'); //   fade-in
    }

    //    input   
    searchInput.addEventListener('input', function() {
        const searchTerm = searchInput.value.toLowerCase(); //           

        //    
        gridItems.forEach(item => {
            const textElement = item.querySelector('.gridtxt'); //       
            const itemName = textElement.textContent.toLowerCase(); //         

            // ,      
            if (itemName.includes(searchTerm)) {
                item.classList.remove('hidden'); //   hidden   
                item.classList.add('fade-in'); //   fade-in    
            } else {
                hideItem(item); //  ,      
            }
        });

        //    ,    
        if (!searchTerm) {
            showAllItems(); //       
        }
    });

    showAllItems(); //          
};

export default search; //   search
